<template>
  <div class="map">
    <MglMap
      :mapStyle="mapStyle"
      @load="onMapLoad"
      :center="center"
      :zoom.sync="zoom"
      :maxBounds="[[4.715857,51.843696], [5.682654,52.349042]]"
      :maxZoom="19"
    >
      <mysearchBar @foundCenter="sendCoordinates" />
      <div class="zoomButton" id="plus" @click="zoomIn">
        <h1>+</h1>
      </div>
      <div class="zoomButton" id="min" @click="zoomOut">
        <h1>--</h1>
      </div>
      <MglGeojsonLayer
        sourceId="provincieLaag"
        layerId="provincieLaag"
        :source="geoJsonSourceProv"
        :layer="geoJsonlayerProv"
      />
      <MglMarker
        v-for="(marker, index) in poi"
        :key="marker.id"
        :coordinates="[ marker.lng , marker.lat]"
        @click="onIconClick($event, marker, index)"
      >
        <img
          class="svgicon mapboxgl-marker mapboxgl-marker-anchor-center"
          slot="marker"
          :class="{ selected : isActive(index) }"
        />
      </MglMarker>
      <div

        class="button luchtfotobutton"
        id="luchtfoto"
        :class="{luchtfotoActive : luchtfotoActive}"
        @click="setLuchtfoto"
      >

      </div>
      <div class="button popupbutton" :class="[popupIsActive ? closeButton: infoButton]" @click="closePopup">
        <svg
          v-if="popupIsActive"
          width="40"
          height="40"
          viewBox="0 0 40 40"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <rect width="8" height="8" />
          <rect x="8" y="8" width="8" height="8" />
          <rect x="16" y="16" width="8" height="8" />
          <rect x="24" y="24" width="8" height="8" />
          <rect x="32" y="32" width="8" height="8" />
          <rect x="24" y="8" width="8" height="8" />
          <rect x="32" width="8" height="8" />
          <rect x="8" y="24" width="8" height="8" />
          <rect y="32" width="8" height="8" />
        </svg>
        <svg
          v-else
          width="6"
          height="26"
          viewBox="0 0 6 26"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <rect x="0.5" y="0.5" width="5" height="5" />
          <rect x="0.5" y="10.5" width="5" height="15" />
        </svg>
      </div>
      <PopUp class="poiPopup" :class="{popupShow: popupIsActive}" :popupIsActive="popupIsActive" :poiData="poiData"></PopUp>
    </MglMap>
  </div>
</template>

<script>
import { MglMarker, MglMap, MglGeojsonLayer } from "@webmapper/vue-mapbox";
import gql from "graphql-tag";
import PopUp from "./popup.vue";
import mysearchBar from "./searcBar.vue";

export default {
  apollo: {
    poi: gql`
      query {
        poi {
          id
          class
          description
          situation
          lng
          lat
        }
      }
    `
  },
  components: {
    MglMarker,
    MglMap,
    MglGeojsonLayer,
    PopUp,
    mysearchBar
  },
  data() {
    return {
      mapStyle:
        "https://api.maptiler.com/maps/96f41b36-a033-4b38-9f22-e2f64fb3868e/style.json?key=BrAJoIaS66eahLFykljd",
      center: [5.1254, 52.082],
      zoom: 10.5,
      poi: {},
      poiData: {},
      popupIsActive: true,
      luchtfotoActive: false,
      closeButton: "closeButton",
      infoButton: "infoButton",
      activeId: null,
      content: "",
      contentTitle: "",
      geoJsonlayerProv: {
        id: "provincieLaag",
        type: "fill",
        source: "provincieLaag",
        paint: {
          "fill-color": "#2c3892",
          "fill-opacity": 0.8
        }
      },
      geojson: {
        type: "geojson",
        data: {
          type: "FeatureCollection",
          features: []
        }
      }
    };
  },
  computed: {
    geoJsonSourceProv() {
      return this.geojson;
    },
    buttonLuchtfotoActive() {
      if (this.zoom >= 14) {
        console.log("zoom groter dan 14");
        return true;
      } else {
        console.log("zoom kleiner dan 14");
        return false;
      }
    }
  },
  methods: {
    async onMapLoad(event) {
      this.map = event.map;
      this.map.addSource("luchtfoto", {
        type: "raster",
        tiles: [
          "https://geodata.nationaalgeoregister.nl/luchtfoto/rgb/wmts/2016_ortho25/EPSG:3857/{z}/{x}/{y}.jpeg"
        ],
        scheme: "xyz"
      });
      this.map.addLayer({
        id: "provincie",
        type: "line",
        source: "wm_visdata",
        "source-layer": "boundaries",
        filter: ["all", ["==", "type", "province"], ["==", "name", "Utrecht"]],
        layout: {
          "line-cap": "round",
          "line-join": "round"
        },
        paint: {
          "line-color": "#2c3892",
          "line-width": 7.5
        }
      });

      this.active = this.poi.map(() => false);
      this.loadProvince();
    },
    async loadProvince() {
      let req = await fetch("/provinciessimpel.geojson");
      let data = await req.json();
      this.geojson.data.features = data.features;
    },
    isActive(index) {
      return index == this.activeId;
    },
    onIconClick(event, data, index) {
      this.activeId = index;
      this.popupIsActive = true;
      let bounds = event.map.getBounds();
      let verschilX = (bounds._ne.lng - bounds._sw.lng) / 4;
      this.poiData = data;
      event.map.easeTo({ center: [data.lng + verschilX, data.lat] });
    },
    closePopup: function() {
      this.popupIsActive = !this.popupIsActive;
    },
    sendCoordinates: function(value) {
      this.center = value.coordinates;
      this.zoom = 14;
    },
    zoomIn() {
      this.zoom = this.zoom + 1;
    },
    zoomOut() {
      this.zoom = this.zoom - 1;
    },
    setLuchtfoto() {
      if (!this.luchtfotoActive) {
        this.map.setPaintProperty("adres_numbers", "text-color",   "#000000"
        );
        this.map.setPaintProperty("path-names", "text-color",
          "#000000"
        );
        this.map.setPaintProperty("road-names", "text-color",

          "#000000"
        );
        this.map.setPaintProperty("nature-labels", "text-color",
          "#000000"
        );
        this.map.setPaintProperty("poi_public_space", "text-color",

          [
            "match",
            ["get", "subtype"],
            "green",
            "#000000",
            "industry",
            "#939393",
            "water",
            "#000000",
            "#eeeeee"
          ]
        );

        this.map.addLayer(
          {
            id: "luchtfoto",
            type: "raster",
            source: "luchtfoto",

            maxzoom: 20
          },
          "adres_numbers"
        );
        this.luchtfotoActive = true;
      } else {
        this.map.removeLayer("luchtfoto");
        this.luchtfotoActive = false;
        this.map.setPaintProperty("adres_numbers", "text-color", "#666666");
        this.map.setPaintProperty("path-names", "text-color", "#928476");
        this.map.setPaintProperty("road-names", "text-color", "#776655");
        this.map.setPaintProperty("nature-labels", "text-color", "#79906c");
        this.map.setPaintProperty("poi_public_space", "text-color", [
          "match",
          ["get", "subtype"],
          "green",
          "#79906c",
          "industry",
          "#939393",
          "water",
          "#a0c8f0",
          "#eeeeee"
        ]);
      }
    }
  }
};
</script>

