          <h1>Beeld gezocht</h1>
          <p>De provincie Utrecht heeft 2019 uitgeroepen tot themajaar Koude Oorlog. Inwoners en bezoekers kunnen dit jaar kennis maken met onzichtbare schuilplaatsen, spannende verhalen en ander cultureel erfgoed uit de periode 1945 – 1991.</p>
          <p>Jarenlang was Europa in de greep van een oorlog de nooit uitbrak: de Koude Oorlog. Een tijd van oost tegen west, spionage en de dreiging van de atoombom. Overheidscampagnes bereidden de bevolking voor op een aanval met kernraketten door de Russen. Op allerlei plekken zijn bunkers en schuilkelders gebouwd om ons te beschermen tegen het communistische ‘Rode gevaar’. Maar ook commandoposten, schuilkelders, vliegtuigshelters, luchtwachttorens en mobilisatiecomplexen. Wat is er in onze provincie nog te vinden over deze tijd?</p>
