<template>
  <div class="searchbar">
    <autocomplete
      :search="search"
      placeholder="Zoek plaats _ _ _"
      aria-label="Zoek plaats _ _ _"
      :get-result-value="getResultValue"
      @submit="handleSubmit"
    ></autocomplete>
  </div>
</template>
<script>
import Autocomplete from "@trevoreyre/autocomplete-vue";
import WKT from "terraformer-wkt-parser";

const suggestUrl = `https://geodata.nationaalgeoregister.nl/locatieserver/suggest?rows=10&fq=provincienaam:Utrecht&fq=type:woonplaats&q=`;
const lookupUrl = `https://geodata.nationaalgeoregister.nl/locatieserver/lookup?fl=id,weergavenaam,centroide_ll,woonplaatsnaam&id=`;

export default {
  name: "mysearchBar",
  data() {
    return {
      woonplaats: {}
    };
  },
  components: {
    Autocomplete
  },
  methods: {
    search(input) {
      const url = suggestUrl + encodeURI(input);
      return new Promise(resolve => {
        if (input.length < 3) {
          return resolve([]);
        }

        fetch(url)
          .then(response => {
            return response.json();
          })
          .then(data => {
            resolve(data.response.docs);
          });
      });
    },
    getResultValue(result) {
      return result.weergavenaam;
    },
    handleSubmit(result) {
      if (result) {
        let getUrl = lookupUrl + result.id;
        fetch(getUrl)
          .then(response => {
            return response.json();
          })
          .then(data => {
            let record = data.response.docs[0];
            let centerCoordinates = WKT.parse(record.centroide_ll);
            this.$emit("foundCenter", centerCoordinates);
          });
      }
    }
  },
  computed: {},
  watch: {}
};
</script>
